#gallery {
  margin-top: 50px;
  display: flex;
  justify-content: center;

  #images {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    row-gap: 32px;
    column-gap: 16px;
    max-width: 1200px;
    margin: 0 100px;
  
    img {
      height: 200px;
      cursor: pointer;
    }

    .a {
      height: 200px;
      width: 200px;
      background-position: center;
      background-size: cover;
    }

    @media screen and (max-width: 777px) {
      & {
        width: min(70%, 400px);
        margin: 0;
      }
      
      img {
        width: 100%;
        height: auto;
      }
    }
  }
}

#overlay {
  opacity: .8;
  position: fixed;
  z-index: 6;
  top: 0px;
  left: 0px;
  background: #707070;
  width: 100%;
  height: 100%;
}

#overlay-slider {
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  position: fixed;
  z-index: 7;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  img {
    min-width: 180px;
    max-height: 60vh;
  }

  .change-image {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    cursor: pointer;
  }

  #close {
    position: absolute;
    top: 32px;
    right: 32px;
    cursor: pointer;

    svg {
      width: 32px;
      height: 32px;
    }
  }
}
