header {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  height: 160px;

  > div {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: min(87%, 1230px);

    &::after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 8px;
      transform: translateX(-50%);
      width: 104%;
      height: 2.5px;
      background-color: var(--color-1);
    }

    h1 {
      color: var(--color-1);
      font-family: 'Dancing Script', cursive;
    }
  }
}