#events {
  margin-top: 50px;
  display: grid;
  row-gap: 50px;

  .events {
    position: relative;
    max-width: 1110px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 16px 1fr;

    .event-container {
      position: relative;

      &.even {
        grid-column: 1;
        justify-self: right;
        margin-right: calc(25% + 8.5px);

        .event-date {
          flex-direction: row-reverse;
        } 

        &::after {
          right: -25%;
        }

        &::before {
          right: calc(-25% - 30px);
        }
      }

      &.odd {
        grid-column: 3;
        justify-self: left;
        margin-left: calc(25% + 9px);

        .event-date {
          flex-direction: row;
        } 

        &::after {
          left: -24.7%;
        }

        &::before {
          left: calc(-24.7% - 30px);
        }
      }

      .event {
        .event-date {
          display: flex;
          column-gap: 4px;

          span {
            line-height: 1.5em;
            color: var(--color-2);

            &:last-child {
              color: #AFAFAF;
              opacity: .8;
              text-transform: capitalize;
            }

            &:nth-child(2) {
              color: #AFAFAF;
              opacity: .8;
            }
          }
        }

        img {
          width: -webkit-fill-available;
        }

        pre {
          color: var(--color-2);
          font-family: 'Roboto', sans-serif;
          white-space: break-spaces;
        }
      }

      &::after {
        content: '';
        position: absolute;
        top: 7px;
        width: calc(25% - 8px);
        height: 8px;
        background-color: var(--color-2);
      }

      &::before {
        content: '';
        position: absolute;
        top: 1px;
        width: 14px;
        height: 14px;
        background-color: var(--color-3);
        border: 4px solid var(--color-2);
        border-radius: 50%;
      }
    }

    .line {
      position: absolute;
      top: 32px;
      left: 4px;
      grid-column: 2;
      width: 8px;
      height: calc(100% + 8px);
      background-color: var(--color-2);
    }

    @media screen and (max-width: 499px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100vw;
      margin: 0;

      .event-container {
        max-width: min(80%, 350px);

        &.even, &.odd {
          margin-left: 0;
          margin-right: 0;

          .event-date {
            flex-direction: row;
          } 
        }

        .event {
          .event-date {
            span {
              font-size: 18px;
            }
          }
        }

        &::after {
          display: none;
        }

        &::before {
          display: none;
        }
      }

      .line {
        display: none;
      }
    }
  }
}