#Home.header {
    width: 100vw;
    height: 100vh;
    position: relative;
    background-image: url("../images/background-1.jpg");
    background-position-x: center;
    background-attachment: fixed;

    img {
        width: calc(min(70vw, 400px));
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        animation: pop-in .7s .1s cubic-bezier(0,1.5,.92,1.6) none;
    }

    @keyframes pop-in {
        0% {
            transform: translate(-50%, -50%) scale(.5);
        }
    
        100% {
            transform: translate(-50%, -50%) scale(1);
        }
    }
}

.frame {
    width: 100vw;
    overflow-x: hidden;
    background-color: var(--color-3);

    > section {
        width: calc(max(min(90vw, 700px), 70vw));
        margin: 48px auto;

        img {
            width: inherit;
        }

        > section {
            margin: 32px 48px;
            color: var(--color-1);
        }
    }

    #menu {
        section {
            display: flex;
            flex-direction: column;
            align-items: center;
            color: var(--color-2);

            #menu-container {
                column-count: 2;
                column-gap: 49px;
                margin: 32px 0 16px 0;

                .menu-category {
                    margin-bottom: 28px;

                    > div {
                        display: flex;
                        flex-direction: column;
                        gap: 8px;

                        > div {
                            display: flex;
                            max-width: 350px;
                            column-gap: 32px;
                            justify-content: space-between;

                            span {
                                text-align: left;
                            }
                        }
                    }

                    h2 {
                        margin-bottom: 12px;
                        font-size: 21px;
                        text-transform: uppercase;
                    }
                }

                @media screen and (max-width: 600px) {
                    column-count: 1;
                }
            }
        }

        .menu-header{
            h1 {
                text-align: center;
                font-size: 81px;
                font-weight: 300;
                font-family: 'Dancing Script', cursive;
            }

            hr {
                margin: 12px;
            }

            .menu-btn {
                display: flex;
                justify-content: space-evenly;
                column-gap: 12px;
                margin-top: 16px;

                button {
                    width: calc(min(100%, 140px));
                    height: 40px;
                    padding: 0 8px;
                    background-color: transparent;
                    color: var(--color-5);
                    font-family: 'Dancing Script', cursive;
                    font-size: 1.5em;
                    border: 2px solid var(--color-5);
                    transition: 0.7s;
                    cursor: pointer;
                }

                @media (pointer: fine) {
                    button:hover {
                        background-color: var(--color-5);
                        color: var(--color-1);
                    }
                }

                @media (hover: hover) {
                    button:active {
                        background-color: var(--color-5);
                        color: var(--color-1);
                        transition: 0s;
                    }
                }
            }

            @media screen and (max-width: 616px) {
                .menu-btn {
                    grid-template-columns: repeat(2, 1fr);

                    button {
                        font-size: 1.25em;
                    }
                }
            }

            @media screen and (max-width: 318px) {
                .menu-btn {
                    grid-template-columns: repeat(1, 1fr);
                }
            }
        }

        .menu-content {
            margin-top: 32px;
            text-align: center;

            h1 {
                font-family: 'Dancing Script', cursive;
                font-size: 32px;
            }
        }
    }

    #about {
        display: grid;
        row-gap: 64px;
        width: inherit;
        height: fit-content;
        margin-bottom: 16px;
        position: relative;

        #text {
            h1 {
                margin-bottom: 16px;
                font-size: 32px;
                font-weight: 400;
                color: var(--color-5);
            }

            p {
                margin-bottom: 16px;
                text-align: justify;
                font-size: 18px;
                line-height: 1.5em;
                color: var(--color-1);
                font-family: 'Dancing Script', cursive;
                font-size: 18px;
            }
        }

        #info {
            > div {
                margin-bottom: 16px;
            }

            > div:nth-child(3) > div {
                margin-bottom: 4px;
            }

            h2 {
                font-size: 16px;
                line-height: 1.5em;
                color: var(--color-5);
            }

            h3 {
                font-size: 16px;
                line-height: 1.4em;
                color: var(--color-6);
            }

            span {
                display: block;
                font-size: 16px;
                font-weight: 300;
                line-height: 1.2em;
            }
        }
    }

    @media screen and (min-width: 1100px) {
        #about {
            grid-template-columns: minmax(auto, 600px) minmax(250px, auto);
        
            #info {
                justify-self: right;
                text-align: right;
            }
        }
    }

    #Gallery {
        h1 {
            text-align: center;
            font-family: 'Dancing Script', cursive;
            font-size: 72px;
        }

        hr {
            margin: 16px 24px 32px 24px;
        }

        #pic-container {
            column-count: 4;
            margin-bottom: 16px;
            
            img {
                width: 100%;
                height: auto;
                margin: 6px 0;
            }
        }
    
        @media screen and (max-width: 777px) {
            #pic-container {
                column-count: 3;
                column-width: 33%;
                column-gap: 7px;
    
                img {
                    margin: 2px 0;
                }
            }
    
            .pics:nth-child(4) {
                display: none;
            }
    
            .pics:nth-child(5) {
                display: none;
            }
    
            .pics:nth-child(8) {
                display: none;
            }
        }
    }
}
