#menu {
  .menu-btn {
    display: flex;
    justify-content: space-evenly;
    column-gap: 12px;
    margin-top: 16px;

    button {
      width: calc(min(100%, 140px));
      height: 40px;
      padding: 0 8px;
      background-color: transparent;
      color: var(--color-5);
      font-family: 'Dancing Script', cursive;
      font-size: 1.5em;
      border: 2px solid var(--color-5);
      transition: 0.7s;
      cursor: pointer;
    }

    @media (pointer: fine) {
      button:hover {
        background-color: var(--color-5);
        color: var(--color-1);
      }
    }

    @media (hover: hover) {
      button:active {
        background-color: var(--color-5);
        color: var(--color-1);
        transition: 0s;
      }
    }
  }

  .menu-content.main {
    h1 {
      margin: 12px 0;
      text-align: center;
      color: var(--color-1);
      font-family: 'Dancing Script', cursive;
      font-size: 64px;
      font-weight: 600;
    }

    #menu-container {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;

      .menu-category {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;

        h2 {
          margin: 8px 0;
          text-align: center;
          color: var(--color-1);
          font-family: 'Dancing Script', cursive;
          font-size: 49px;
          font-weight: 400;
          text-transform: capitalize;
        }

        section {
          position: relative;
        }

        .slider {
          overflow-x: hidden;

          .btn {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            fill: var(--color-2);
            cursor: pointer;
          }

          ul {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            column-gap: 80px;
            margin-bottom: 60px;
            transition: all .4s linear;
  
            li {
              position: relative;
              display: flex;
              flex-direction: column;
              justify-content: space-between;
              min-height: 120px;
              box-sizing: border-box;
              margin-top: 128px;
              padding-left: 16px;
              padding-right: 16px;
              padding-bottom: 10px;
              background-color: var(--color-2);
              border-radius: 25px;
  
              img {
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                // width: -webkit-fill-available;
              }

              div {
                span {
                  display: block;
                }
  
                span:first-child {
                  margin-bottom: 7px;
                  font-size: 21px;
                  font-weight: 600;
                  text-transform: capitalize;
                }
              }
  
              > span {
                line-height: 2em;
              }
            }
          }
        }
      }
    }
  }
}