#page-container {
  position: relative;
  min-height: 100vh;

  #content-wrap {
    min-height: calc(100vh - 200px);
    padding-bottom: 200px;  

    > div {
      min-height: inherit;
    }

    .content {
      padding: 16px 0 75px 0;
    }
  }
}

nav {
  width: 100vw;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  z-index: 1;
  background-color: transparent;
  transition: 0.6s ease-in-out;

  &.active {
    position: fixed;
    z-index: 7;
    left: 0;
    top: 0;
    background-color: var(--color-7);

    a, svg {
      opacity: 0;
      animation: appear .6s ease-in-out forwards;
    }

    @keyframes appear {
      100% {
        opacity: 1;
      }
    }
  }

  ul {
      display: flex;
      list-style: none;

      a {
        height: 42px;
        line-height: 43px;
        margin: 2px 16px;
        padding: 0px 8px;
        display: flex;
        background-color: transparent;
        font-size: 1.25em;
        font-weight: 400;
        color: var(--color-1);
        transition: .7s ease;
        letter-spacing: 1px;
        text-transform: uppercase;
        text-shadow: 0 0 9px var(--color-1);
        text-decoration: none;

        &.active {
          color: var(--color-6);
          text-shadow: 0 0 10px var(--color-6);
        }

        &:hover {
          color: var(--color-5);
          text-shadow: 0 0 10px var(--color-5);
        }
      }
  }

  svg {
    display: none;
    padding: 4px;
    opacity: 0;
    position: absolute;
    top: 16px;
    left: 16px;
    fill: var(--color-1);
    cursor: pointer;
  }
}

@media screen and (max-width: 700px) {
  nav {
    justify-content: space-between;
    height: 46px;

    &.active {
      position: fixed;
      z-index: 1;
      left: 0;
      top: 0;
      background-color: transparent;
    }

    &.open {
      width: 100vw;
      height: 100vh;
      position: fixed;
      opacity: .9;
      background-color: var(--color-7);

      .menu {
        display: block;
        margin: 0 auto;

        li a {
          justify-content: space-around; 
        }
      }
    }

    svg {
      display: block;
      animation: fade_in 1.4s linear forwards;
    }

    @keyframes fade_in {
      100% {
        opacity: 1;
      }
    }

    .menu {
      display: none;
    }
  }
}

footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 200px;
  background-color: var(--color-7);
  color: var(--color-1);

  span {
    font-size: 20px;
    font-weight: 300;
  }

  svg {
    animation: disappear .5s ease-in-out forwards;
  }

  svg.active {
    animation: pop_up .5s ease-in-out forwards;
  }

  @keyframes pop_up {
    0% {
      transform: translate3d(0px, -81px, 0px) scale(0.5, 0.5);
    }
    100% {
      transform: translate3d(0px, -118px, 0px);
    }
  }

  @keyframes disappear {
    0% {
      transform: translate3d(0px, -118px, 0px);
    }
    100% {
      transform: translate3d(0px, -81px, 0px) scale(0.5, 0.5);
    }
  }

  .btn-top {
    width: 24px;
    height: 24px;
    position: absolute;
    cursor: pointer;
    transform: translate3d(0px, -81px, 0px) scale(0.7, 0.7);

    svg:nth-child(1) {
      position: absolute;
      fill: var(--color-3);
      animation: arrows_down_1 .5s linear forwards;
      transition: .7s;
    }

    svg:nth-child(2) {
      position: absolute;
      fill: var(--color-3);
      animation: arrows_down_2 .5s linear forwards;
      transition: .7s;
    }

    svg.active:nth-child(1) {
      fill: var(--color-3);
      animation: arrows_up_1 .7s ease forwards;
    }

    svg.active:nth-child(2) {
      fill: var(--color-3);
      animation: arrows_up_2 .7s .1s cubic-bezier(.37,1.11,.88,1.05) forwards;
    }

    @keyframes arrows_down_1 {
      0% {
        opacity: 1;
        top: -76px;
      }
      100% {
        opacity: 0;
        top: 0;
      }
    }

    @keyframes arrows_down_2 {
      0% {
        opacity: 1;
        top: -64px;
      }
      100% {
        opacity: 0;
        top: 0;
      }
    }

    @keyframes arrows_up_1 {
      0% {
        opacity: 0;
        top: 0;
      }
      100% {
        opacity: 1;
        top: -76px;
      }
    }

    @keyframes arrows_up_2 {
      0% {
        opacity: 0;
        top: 0;
      }
      100% {
        opacity: 1;
        top: -64px;
      }
    }
  }

  @media (pointer: fine) {
    .btn-top:hover svg {
      fill: var(--color-5) !important;
    }
  }

  @media (hover: hover) {
    .btn-top:active svg {
      fill: var(--color-5) !important;
      transition: 0s;
    }
  }
}
